* {
    box-sizing : border-box;
}

body {
    margin : 0;
    padding : 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color : #eff3f7;
    height : 100%;
}

.container {
    height : 100%;
    display : flex;
    justify-content: center;

}

.loader {
    font-size : 20pt;
    width : 100%;
    height : 100%;
    display: flex;
    justify-content : center;
    align-items: center;
}

.movies {
    display : flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding : 50px;
    padding-top: 70px;
    width : 80%;
}

.movies .movie {
    width : 45%;
    background-color : white;
    margin-bottom : 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight:300;
    padding: 20px;
    position: relative;
    color: #b2b3bc
}

.movie img{
    position: relative;
    top:-50px;
    max-width :120px;
    margin-right: 30px;
    box-shadow: 3px 3px gray;
}

.movie .movie__title, 
.movie .movie__year {
    margin: 0;
    font-weight:300;

}

.movie .movie__title {
    margin-bottom: 5px;
    font-size : 18pt;
    font-weight: 700;
    color: #2c2c2c
}

.movie .movie__genres {
    list-style: none;
    padding:0;
    margin:0;
    display:flex;
    margin:5px;
}

.movie__genres li{
    margin-right : 8px;
    font-size : 12pt;
}